<template>
  <transition name="change">
    <div class="tab-wrapper" v-if="isNavRouter">
    <!-- <div class="tab-wrapper" v-if="isNavRouter"> -->
      <div class="tab">
        <router-link tag="div" class="tab-item" active-class="active" to="/home" :class="{'active': subIsActive('/search')}">
          <i class="icon iconfont icon-home"></i><span>首页</span>
        </router-link>
        <router-link tag="div" class="tab-item" active-class="active" to="/classification">
          <i class="icon iconfont icon-fenlei"></i><span>分类</span>
        </router-link>
        <router-link tag="div" class="tab-item" active-class="active" to="/shopping">
          <i class="icon iconfont icon-gouwuche"></i><span>购物车</span>
        </router-link>
        <router-link tag="div" class="tab-item" active-class="active" to="/me">
          <i class="icon iconfont icon-icon-test"></i><span>我</span>
        </router-link>
      </div>
    </div>
  </transition>
</template>
<script>
import { mapGetters } from 'vuex'
export default {
  name: 'tab',
  data () {
    return {
    }
  },
  created () {
  },
  computed: {
    ...mapGetters([
      'isNavRouter'
    ])
  },
  methods: {
    subIsActive (input) {
      const paths = Array.isArray(input) ? input : [input]
      return paths.some(path => {
        return this.$route.path.indexOf(path) === 0 // current path starts with this path string
      })
    }
  }
}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
@import "~common/scss/iconfont";
@import "~common/scss/variable";
@import "~common/scss/minxin";
@import "~common/scss/base";
.tab-wrapper {
  position: fixed;
  z-index: 9999;
  bottom: 0;
  left: 0;
  right: 0;

  background: #fff;
  box-shadow: 0 3px 14px 2px rgba(0,0,0,.12);
  .tab {
    display: flex;
    height: 48px;

    @include border-top-1px($color-border);
      .tab-item {
        display: flex;
        flex: 1;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        color: $color-text-title;
      .icon {
        font-size: $font-size-large;
      }
      span {
        font-size: $font-size-medium;
        margin-top: 4px;
      }
    }
    .active {
      color: $color-theme;
    }
  }
  &.change-enter, &.change-leave-to {
    -webkit-transform:translateY(100%);
    transform:translateY(100%);
  }
  &.change-enter-active, &.change-leave-active{
    transition: all .2s ease-out;
  }
}
</style>



