const state = {
  isNavRouter: true // 是否显示首页的导航
}

// getters
const getters = {
  isNavRouter: state => {
    return state.isNavRouter
  }
}

// actions
const actions = {
}

// mutations
const mutations = {
  set_isNavRouter (state, value) {
    state.isNavRouter = value
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}



